.wrap {
    width: 80vw;
    height: 100vh;
    overflow: hidden;
    position: relative;

}
.center-wrap {
    /* width: 300px; */
    margin: auto;
}
.isanelle {
    width: 500px;
}
.upload-btn {
    position: absolute;
    right: 150px;
    border: 1px dashed wheat;
    top: 400px;
    width: 400px;
    bottom: 200px;
    background: white;
    border-radius: 8px;
    font-size: 25px;
    text-align: center;
    padding-top: 60px;
    box-shadow: 1px 1px 1px 1px wheat;
    cursor: pointer;
}
.speech {
    width: 200px;
}
.logo {
    position: absolute;
    top: 200px;
    right: 300px;
    width: 150px;
    animation:myRotate 3s cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite;
    transform-origin: top;
}
 
@keyframes myRotate{
    0%{ transform: rotate(0deg);}
    25%{ transform: rotate(180deg);}
    50%{ transform: rotate(-45deg);}
    75%{ transform: rotate(0deg);}
    100%{ transform: rotate(360deg);}
}