.wrap {
    display: flex;
    justify-content: space-between;
    padding-top: 5vh;
}
.left-wrap, .right-wrap {
    /* border: 1px solid red; */
    text-align: center;
    background: grey;
}

.left-wrap {
    width: 40%;
    height: 500px;
    display: flex;
}

.left-wrap  > img {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
    margin: auto;
}



.right-wrap {
    position: relative;
    display: flex;
    width: 40%;
    height: 500px;
}

.canvas-container  canvas {
    max-width: 100%;
    /* max-height: 100%; */
    width: auto;
    height: auto;
    margin: auto;
    max-height: 500px
}

.tools {
    padding: 10px;
    margin-top: 3vh;
    background: white;
    display: flex;
    border-radius: 8px;
    justify-content: space-around;
    align-items: center;
    line-height: 24px;
}
.tools .mabel {
    width: 100px;
}
.tools .plan-num {
    width: 30%;
}
.tools .qrcode-info {
    width: 30%;
}
.tools .tip {
    color: red;
    font-size: 12px;
}
.canvas-container {
    position: relative;
    /* height: 500px; */
    margin: auto;
    max-height: 500px;
    display: inline-block;
}
.line-canvas {
    position: absolute;
    top: 0;
    left: 0;
    z-index:1;
    margin: auto;
    background:rgba(255,255,255,0);
}